.skill
  width: 320px
  background-color: rgba(204, 204, 204, .1)
  display: flex
  flex-direction: column
  align-items: center
  border-radius: 10px
  padding: 20px 7px
  box-shadow: 0 0 25px rgb(0 0 0)

  .icon
    width: 140px
    height: 100px
    background: no-repeat center center

  .title
    color: white

  .description
    color: white
    font-size: 16px
    text-align: center

