.item
  width: 350px
  display: flex
  flex-direction: column
  align-items: center
  border-radius: 10px
  padding: 15px 15px
  box-shadow: 0 0 25px rgb(0 0 0)

  .imgContainer
    width: 100%
    height: 300px
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    border-radius: 15px
    background-size: cover
    background: no-repeat center center
    position: relative

  .imgContainer::before
    position: absolute
    content: ""
    background-color: black
    border-radius: 15px
    top: 0
    bottom: 0
    left: 0
    right: 0
    opacity: .4

  .buttonProject
    display: flex
    flex-direction: column
    align-items: inherit
    width: 40px
    height: 40px
    border: 2px solid #ff651c
    border-radius: 50%
    position: absolute
    z-index: 1
    top: 20px
    left: 20px

  .buttonProject:hover
    box-shadow: 0 0 40px #ff651c
    transition: .2s
    cursor: pointer

  .text
    padding: 0 10px
    display: flex
    flex-direction: column
    align-items: center
    .title
      color: white
      font-weight: 700
      margin-bottom: 15px
      font-size: 24px
    .description
      text-align: center
      color: white
      line-height: 24px
      font-size: 16px








